<template>
    <div class="wrap">
        <div class="mark">
            <form action="/v1/app/changeSwiper"  ref="form" target="stop" method="POST" novalidate enctype="multipart/form-data">
                <label>
                    <span>ID</span>
                   <el-input name="SwiperId" :value="input1"></el-input>
                </label>
                <label>
                    <span>图片信息</span>
                    <el-input v-model="input" name="swiperdesc" placeholder="请输入内容"></el-input>
                </label>
                <label>
                    <span>上传图片</span>
                    <div class="iconn" ref="iconn">
                        +
                        <img :src="url" alt="">
                        </div>
                    <input type="file" class="file" name="swiperPicUrl" @change="files($event)">
                </label>
                <div class="btn">
                    <el-button type="submit" @click="submitForm($event)" :plain="true">提交</el-button>
                    <el-button  @click="exit">取消</el-button>
                    <iframe src="" name="stop" frameborder="0" style="display: none;"></iframe>
                </div>
            </form>
        </div>
    </div>
</template>
<script>
export default {
      name: "add",
  data() {
    return {
      input: "",
      input1: '',
      url: "",
    };
  },
  props: ['message'],
  created() {
    this.input = this.message.name;
    this.url = this.message.url;
    this.input1 = this.message.id;
  },
  methods: {
    exit() {
      this.$emit("exit2");
    },
    submitForm(event) {
        this.$refs.form.submit()
        this.$message({
          message: '恭喜你，修改成功',
          type: 'success'
        });
        this.$emit('exit2');
        this.$router.go()

    },
    files(ev) {
        this.$refs.iconn.style.fontSize = '0';
      this.url = window.URL.createObjectURL(event.target.files[0]);
    },
  }
}
</script>
<style lang="less" scoped>
    @import url('./style.less');
</style>




