import Axios from 'axios';
import qs from 'qs';

Axios.defaults.timeout = 10000;

const env = process.env.NODE_ENV;

// 设置开发、生产环境请求默认地址
if(env === 'development') {
    // Axios.defaults.baseURL = 'http://39.96.91.108:80';
    Axios.defaults.baseURL = 'http://192.168.0.129:3005';    
    
    
} else {
    Axios.defaults.baseURL = 'http://localhost:3005';    
    
}

