function post(url, parms) {
    return new Promise((resolve, reject) => {
        Axios.post(url, qs.stringify(parms)).then(res => {
            resolve(res);
        }).catch(err => {
            reject(err);
        })
    })
};

function get(url, parms) {
    return new Promise((resolve, reject) => {
        Axios.get(url, {
            parms
        }).then(res => {
            resolve(res);
        }).catch(err => {
            reject(err);
        })
    })
}

export  {
    post,
    get
}


