<template>
    <div class="add">
        <div class="mark">
            <form action="/v1/app/addNotebook"  ref="form" target="stop" method="POST" novalidate enctype="multipart/form-data">
                <label>
                    <span>标题信息</span>
                    <el-input v-model="TelTitle" name="NotebookTitle" placeholder="请输入标题"></el-input>
                </label>
                  <label>
                    <span>详情信息</span>
                    <el-input v-model="TelDesc" name="Notebookdesc" placeholder="请输入详情"></el-input>
                </label>
                       <label>
                    <span>现价</span>
                    <el-input v-model="price" name="price" placeholder="请输入现价"></el-input>
                </label>
                <label>
                    <span>原价</span>
                    <el-input v-model="Activeprice" name="Activeprice" placeholder="请输入原价"></el-input>
                </label>
                <label>
                    <span>上传图片</span>
                    <div class="iconn" ref="iconn">
                        +
                        <img :src="TelPic" alt="">
                        </div>
                    <input type="file" class="file" name="NotebookPicUrl" @change="files($event)">
                </label>
                <div class="btn">
                    <el-button type="submit" @click="submitForm($event)" :plain="true">提交</el-button>
                    <el-button  @click="exit">取消</el-button>
                    <iframe src="" name="stop" frameborder="0" style="display: none;"></iframe>
                </div>
            </form>
        </div>
    </div>
</template>

<script>
export default {
  name: "add",
  data() {
    return {
      TelTitle: "",
      TelDesc: "",
      price: "",
      Activeprice: "",
      TelPic: "",
    };
  },
  methods: {
    exit() {
      this.$emit("exit", false);
    },
    submitForm(event) {
      this.$refs.form.submit();
      this.$message({
        message: "恭喜你，上传成功",
        type: "success"
      });
      this.$emit("exit", true);
      this.$router.go();
    },
    files(ev) {
      this.$refs.iconn.style.fontSize = "0";
      this.TelPic = window.URL.createObjectURL(event.target.files[0]);
    }
  }
};
</script>
<style lang="less" scoped>
@import url("./style.less");
</style>





