<template>
    <div class="swiper">
            <div class="right-top">
                <h3 >电视列表页</h3>
                 <el-button plain type="primary" icon="el-icon-circle-plus" @click="open5"></el-button>
            </div>
        <div class="right-sub">
            <ul class="title-list-text">
                <li>id</li>
                <li>标题</li>
                <li>详情</li>
                <li>路径</li>
                <li>原价</li>
                <li>现价</li>
                <li>操作</li>
            </ul>
            <ul v-for="(item, index) in tvList" :key="index">
                <li>{{item.id}}</li>
                <li>{{item.TVTitle}}</li>
                <li>{{item.TVDesc}}</li>
                <li>{{item.TVPic}}</li>
                <li>{{item.price}}</li>
                <li>{{item.Activeprice}}</li>
                <li>
                    <el-button type="primary" icon="el-icon-edit" :uid="item.id" @click="revision(item.id)"></el-button>
                    <el-button type="primary" icon="el-icon-delete" :uid="item.id" @click="deleted(item.id)"></el-button>
                </li>
            </ul>
            </div>
        <transition name="el-zoom-in-bottom">
        <Add v-show="flag" @exit="exit" ></Add>
      </transition>
        <el-collapse-transition>
        <Cont v-if="flag2"  @exit2="exit2" :message="message"></Cont>
      </el-collapse-transition>
    </div>
</template>
<script>
import Add from './add/index.vue'
import Cont from './cont/index.vue'
export default {
  name: "swiper",
  data() {
      return {
          tvList: null,
        flag: false,
        flag2: false,
        message: {
         id: '',
        TVTitle: "",
        TVDesc: "",
        price: "",
        Activeprice: "",
        TVPic: "",
        }
      }
  },
  created() {
      this.$axios.post('/v1/app/findTV',{}).then(res => {
          this.tvList = res.data.msg
        //   console.log(res.data.msg)
      })
  },
  props: ["swiperList"],
  components: {
      Add,
      Cont
  },
  methods: {
    // 新增
    open5() {
        this.flag = true
    },
    exit(flag) {
        this.flag = false
    },
   exit2() {
        this.flag2 = false
    },
    // 修改
    revision(id) {
        this.flag2 = true
        this.tvList.forEach(item => {
         if(item.id === id) {
            this.message.id = item.id            
            this.message.TVTitle = item.TVTitle
            this.message.TVDesc = item.TVDesc
            this.message.price = item.price
            this.message.Activeprice = item.Activeprice
            this.message.TVPic = item.TVPic
         }
     });
    },

    // 删除
    deleted(id) {
           this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
        this.$axios.post('/v1/app/delTV', { TVId: id}).then(res => {
        //   console.log(res)
      })
          this.$message({
            type: 'success',
            message: '删除成功!'
          });
          this.$router.go()
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });          
        });
      
    }
  },
  mounted() {}
};
</script>
<style lang="less" scoped>
@import url("./style.less");
</style>




