import Vue from 'vue'
import App from './App'
import router from './router'
import * as Server from './api/index.js'
import ElementUI from "element-ui"
import 'element-ui/lib/theme-chalk/index.css'
Vue.prototype.$axios = Server;
Vue.use(ElementUI)

Vue.config.productionTip = false

new Vue({
  el: '#app',
  router,
  components: { App },
  template: '<App/>'
})



