<template>
    <div class="wrap">
        <div class="mark">
            <form action="/v1/app/changeNav"  ref="form" target="stop" method="POST" novalidate enctype="multipart/form-data">
                <label>
                    <span>ID</span>
                   <el-input name="id" :value="input1"></el-input>
                </label>
                <label>
                    <span>修改接口</span>
                    <el-input v-model="input" name="title" placeholder="请输入内容"></el-input>
                </label>
                <div class="btn">
                    <el-button type="submit" @click="submitForm($event)" :plain="true">提交</el-button>
                    <el-button  @click="exit">取消</el-button>
                    <iframe src="" name="stop" frameborder="0" style="display: none;"></iframe>
                </div>
            </form>
        </div>
    </div>
</template>
<script>
export default {
      name: "add",
  data() {
    return {
      input: "",
      input1: '',

    };
  },
  props: ['message'],
  created() {
    this.input = this.message.title;
    this.input1 = this.message.id;
  },
  methods: {
    exit() {
      this.$emit("exit2");
    },
    submitForm(event) {
        this.$refs.form.submit()
        this.$message({
          message: '恭喜你，上传成功',
          type: 'success'
        });
        this.$emit("exit2");
        this.$router.go()
    },
    files(ev) {
        this.$refs.iconn.style.fontSize = '0';
      this.url = window.URL.createObjectURL(event.target.files[0]);
    },
  }
}
</script>
<style lang="less" scoped>
    @import url('./style.less');
</style>




