<template>
    <div class="swiper">
            <div class="right-top">
                <h3 >顶部菜单列表页</h3>
                 <el-button plain type="primary" icon="el-icon-circle-plus" @click="open5"></el-button>
            </div>
        <div class="right-sub">
            <ul class="title-list-text">
                <li>id</li>
                <li>图片</li>
                <li>路径</li>
                <li>操作</li>
            </ul>
            <ul v-for="(item, index) in topMenuList" :key="index">
                <li>{{item.id}}</li>
                <li>{{item.menuDesc}}</li>
                <li>{{item.menuPic}}</li>
                <li>
                    <el-button type="primary" icon="el-icon-edit" :uid="item.id" @click="revision(item.id)"></el-button>
                    <el-button type="primary" icon="el-icon-delete" :uid="item.id" @click="deleted(item.id)"></el-button>
                </li>
            </ul>
            </div>
      <transition name="el-zoom-in-bottom">
        <Add v-show="flag" @exit="exit" ></Add>
      </transition>
        <el-collapse-transition>
        <Cont v-if="flag2"  @exit2="exit2" :message="message"></Cont>
      </el-collapse-transition>
            
    </div>
</template>
<script>
import Add from './add/index.vue'
import Cont from './cont/index.vue'
export default {
  name: "swiper",
  data() {
      return {
        currentPage: 2,
        total:0,
        a: 0,
        arr: [],
        topMenuList: null,
        flag: false,
        flag2: false,
        message: {
            id: '',
            name: '',
            url: ''
        }
      }
  },
  components: {
      Add,
      Cont
  },
created() {
      this.$axios.post('/v1/app/findMenu', {}).then(res => {
          this.topMenuList = res.data.msg
          this.total = this.topMenuList.length
      })
  },
  methods: {
    // 新增
    open5() {
        this.flag = true
    },
    exit(flag) {
        this.flag = false
    },
   exit2() {
        this.flag2 = false
    },
    // 修改
    revision(id) {
        this.flag2 = true
        this.topMenuList.forEach(item => {
         if(item.id === id) {
            this.message.id = item.id            
            this.message.name = item.menuDesc
            this.message.url = item.menuPic
         }
     });
    },

    // 删除
    deleted(id) {
           this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
        this.$axios.post('/v1/app/delMenu', { MenuId: id}).then(res => {
        //   console.log(res)
      })
          this.$message({
            type: 'success',
            message: '删除成功!'
          });
          this.$router.go()
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });          
        });
    },
  },
  mounted() {}
};
</script>
<style lang="less" scoped>
@import url("./style.less");
</style>




