import Vue from 'vue'
import Router from 'vue-router'
import Index from '../components/index'
import Swiper from '../components/swiper'
import mainNav from '../components/main-nav'
import nodeBook from '../components/node-book'
import Phone from '../components/phone'
import topMenu from '../components/top-menu'
import Tv from '../components/tv'
import Household from '../components/household'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      redirect: 'swiper/0'
    },
    {
      path: '/',
      name: 'index',
      component: Index,
      children: [
        {
          path: 'swiper/:id',
          name: 'swiper',
          component: Swiper,
        },
        {
          path: 'mainnav/:id',
          name: 'main-nav',
          component: mainNav,
        },
        {
          path: 'nodebook/:id',
          name: 'node-book',
          component: nodeBook,
        },
        {
          path: 'topmenu/:id',
          name: 'top-menu',
          component: topMenu,
        },
        {
          path: 'tv/:id',
          name: 'tv',
          component: Tv,
        },
        {
          path: 'household/:id',
          name: 'household',
          component: Household,
        },
        {
          path: 'phone/:id',
          name: 'phone',
          component: Phone,
        },
      ]
    }
  ]
})



