<template>
    <div class="wrap">
      <div class="top">
          小米商城后台管理系统
      </div>
      <div class="content">
        <div class="left-nav">
          <ul>
            <router-link v-for="(item, index) in navList" 
            :key="index" tag="li" 
            :to="item.path+'/'+ index" 
            :class="item.flag ? 'active' : ''" 
            >
            <div @click="resualt(index)">{{item.txt}}</div>
            </router-link>
          </ul>
        </div>
        <div class="right-content" ref="ccc">
          <router-view :swiperList="swiperList"></router-view>
        </div>
      </div>
    </div>
</template>
<script>
export default {
  name: "index",
  data() {
    return {
      title: "",
      swiperList: [],
      navList: [
        {
          txt: "轮播图",
          path: "/swiper",
          flag: true
        },
        {
          txt: "主导航",
          path: "/mainnav",
          flag: false
        },
        {
          txt: "顶部菜单",
          path: "/topmenu",
          flag: false
        },
        {
          txt: "手机",
          path: "/phone",
          flag: false
        },
        {
          txt: "电视",
          path: "/tv",
          flag: false
        },
        {
          txt: "笔记本",
          path: "/nodebook",
          flag: false
        },
        {
          txt: "家电",
          path: "/household",
          flag: false
        }
      ]
    };
  },
  created() {
    this.xr();
    let req = /[0-9]/;
      if(req.test(location.hash)) {
        let ind = location.hash.match(req)[0]
          this.navList.forEach(item => {
        item.flag = false;
      })
      this.navList[ind].flag = true;
    }
  },
  methods: {
    // 导航栏的点击事件集合
    resualt(index) {      
      this.navList.forEach(item => {
        item.flag = false;
      });
      this.navList[index].flag = true;
      const loading = this.$loading({
          target: this.$refs.ccc,
          lock: true,
          text: 'Loading',
          spinner: 'el-icon-loading',
          background: 'rgba(0, 0, 0, 0.7)'
        });
        setTimeout(() => {
          loading.close();
        }, 500);
    },

    xr: function() {
      this.$axios.post('/v1/app/findSwiper', {}).then(res => {
      if(res.status === 200 && !res.data.status) {
        this.swiperList = res.data.msg
        this.$loading({}).close()
      }
    })
    }
   },
};
</script>
<style lang="less" scoped>
@import url("./style.less");
</style>




