<template>
    <div class="swiper">
            <div class="right-top">
                <h3 >主导航列表页</h3>
                 <el-button plain type="primary" icon="el-icon-circle-plus" @click="open5"></el-button>
            </div>
        <div class="right-sub">
            <ul class="title-list-text">
                <li>id</li>
                <li>接口</li>
                <li>操作</li>
            </ul>
            <ul v-for="(item, index) in navList" :key="index">
                <li>{{item.id}}</li>
                <li>{{item.title}}</li>
                <li>
                    <el-button type="primary" icon="el-icon-edit" :uid="item.id" @click="revision(item.id)"></el-button>
                    <el-button type="primary" icon="el-icon-delete" :uid="item.id" @click="deleted(item.id)"></el-button>
                </li>
            </ul>
            </div>
            <transition name="el-fade-in-linear">
                <Add v-show="flag" @exit="exit"></Add>
            </transition>
            <transition name="el-zoom-in-bottom">
                 <Cont v-if="flag2"  @exit2="exit2" :message="message"></Cont>
            </transition>
           
    </div>
</template>
<script>
import Add from './add/index.vue'
import Cont from './cont/index.vue'
export default {
  name: "mainNav",
  data() {
      return {
          navList: [],
        flag: false,
        flag2: false,
        message: {
            id: '',
            title: ''
        }
      }
  },
  created() {
      this.$axios.post('/v1/app/allNav', {}).then(res => {
          this.navList = res.data.msg
      })
  },
  components: {
      Add,
      Cont
  },
  methods: {
    // 新增
    open5() {
        this.flag = true
    },
    exit(flag) {
        this.flag = false
    },
   exit2() {
        this.flag2 = false
    },
    // 修改
    revision(id) {
        this.flag2 = true
        this.navList.forEach(item => {
         if(item.id === id) {
            this.message.id = item.id            
            this.message.title = item.title           
         }
     });
    //  console.log(this.message)
    },

    // 删除
    deleted(id) {
           this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
        this.$axios.post('/v1/app/delNav', { navId: id}).then(res => {
        //   console.log(res)
      })
          this.$message({
            type: 'success',
            message: '删除成功!'
          });
          this.$router.go(

          )
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });          
        });
      
    }
  },
  mounted() {}
};
</script>
<style lang="less" scoped>
@import url("./style.less");
</style>




